<template>
    <div id="articles">

    <section class="hero is-primary " >
        <div class="hero-body">
            <div class="container">
                <div class="columns is-centered" >
                    <div class="column">

                        <div class = "container is-fluid search">
                            <!-- <div class="columns   is-1" id="second_col"> -->
                                <!-- <div class="column is-2" id="tag_select"  > -->
                                    <div class="select  is-primary"  id="se_id">
                                        <select  id="se"  v-model="selected">
                                            <option v-for="tag in sort" :key="tag">
                                                {{ tag }}
                                            </option>
                                        </select>
                                    </div>
                                <!-- </div> -->
                                <!-- <div class="column is-10" id="fitler">
                                    <input class="input " type="text" placeholder="Text input">
                                </div> -->
                            <!-- </div> -->
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>

    <section class="hero">
        <div class="hero-body">


        <div class="calatog" v-for="article in artlist_list" :key="article.title">
            <div class = "container is-fluid ">
                <div class="columns   is-1" id="second_col">
                    <div class="column is-8 is-12-mobile article_title has-text-left">
                        <a v-bind:href="article.link">{{ article.title}}</a>
                    </div>

                    <div class="column is-2  article_tag is-hidden-mobile has-text-left">
                        <p>{{ article.tag}}</p>
                    </div>

                    <div class="column is-2  article_date is-hidden-mobile has-text-left">
                        <p>{{ article.date}}</p>
                    </div>
                </div>
            </div>
        </div>

        
        </div>
    </section>

    </div>
</template>



<script>
// import ARTI from './../data/articles'

export default {
    data(){
        return{
            articles:[
                {
                tag:"esp8266",
                title: "自定义ESP8266 SmartConfig",
                date:"2018-1-16",
                link:"./article/esp8266/esp8266_user_define_smartconfig/esp8266_user_define_smartconfig.html"
                },
                {
                    tag:"python,web",
                    title: "Chrome bookmarts to web",
                    date:"2018-2-16",
                    link:"./article/esp8266/peripheral/bookmarts.html"
                },
                {
                    tag:"stm8,ble",
                    title: "BT05 IOT",
                    date:"2018-3-16",
                    link:"https://www.halin.xyz/src/project/BT05_IOT/bt05_iot.html"
                },
                {
                    tag:"python",
                    title: "a simple spider for tumblr",
                    date:"2018-12-16",
                    link:"https://www.halin.xyz/src/python/spider_for_tumblr/simple_spider_for_tumblr.html"
                },
                {
                    tag: "ESP32",
                    title: "电动手敲琴",
                    date: "2018-12-27",
                    link: "./article/log/Finger_piano/Finger_piano.html"
                }
            ],
            selected:"all",
            artlist_list:[],
            sort:[]
        }
    },
    created(){
        console.log("Created");
        console.log(this.articles);
        console.log(this.articles.length);
        this.sort.push("all");
        for(var i=0;i<this.articles.length;i++)
        {
            var str = this.articles[i].tag;
            var tag_list = str.split(',');
            for(var j=0;j<tag_list.length;j++)
            {
                console.log(this.sort.indexOf(tag_list[j]));
                if( this.sort.indexOf(tag_list[j]) == -1)
                    this.sort.push(tag_list[j]);
            }
            this.artlist_list.push(this.articles[i]);
        }
        console.log(this.sort);
        console.log("The article list:");
        console.log(this.artlist_list);
    },
    watch:{
        selected:function(val){
            console.log("watch:"+val);
            this.artlist_list = [];
            if(val != "all")
            {
                for(var i=0;i<this.articles.length;i++)
                {
                    var str = this.articles[i].tag;
                    var tag_list = str.split(',');
                    console.log(tag_list.indexOf(val));
                    if(tag_list.indexOf(val) != -1)
                        this.artlist_list.push(this.articles[i]);
                }                
            }else{
                for(var i=0;i<this.articles.length;i++)
                {
                    this.artlist_list.push(this.articles[i]);
                }  
            }
        }
    }
}
</script>



<style scoped>
#se{
    width: 100%;
}

#se_id{
    width: 100%;
}

.search .column{
    padding: 0;
    margin-bottom: 5px;
}

.search{
    margin-bottom: 20px;
}

</style>

